import { ReactNode } from "react";
import { Info } from "@mui/icons-material";
import {
  Box,
  Button,
  Chip,
  IconButton,
  TextField,
  Typography,
} from "@mui/material";
import ReactHtmlParser from "html-react-parser";

import {
  Attach,
  Bolt,
  Bot,
  Clock,
  Emoji,
  GeoLocation,
  Send,
  UserAdd,
} from "Assets/Icons";
import {
  ChatArea,
  ChatBoxAbove,
  ChatBoxBelow,
  ChatBoxFooter,
  ChatBoxStart,
  ChatBoxStartActionSx,
  ChatFooterEnd,
  ChatFooterStart,
  ChatHeader,
  ChatHeaderEnd,
  ChatHeaderStart,
  ChatInfoFooter,
  ChatMainContent,
  ChatTitle,
  FooterDescription,
  FooterHeading,
  LabeledIconButton,
  Message,
  MessageIn,
  MessageOut,
  MessageSx,
  TimeBadge,
} from "Containers/Engage/Chat/LayoutComponents";
import { useChatApi } from "Contexts/Engage";

import ChatInfo from "./Info";

type ChatBoxAction = {
  icon: ReactNode;
};

const ChatMain = () => {
  const { setShowInfoTab } = useChatApi();
  // const randomShowChatBoxOrInfo = Math.random() < 0.5;
  const ChatBoxActions: ChatBoxAction[] = [
    { icon: <Emoji /> },
    { icon: <Attach /> },
    { icon: <GeoLocation /> },
    { icon: <Bolt /> },
  ];
  const formatMessage = (message: string) =>
    ReactHtmlParser(message.replaceAll(/(?:\r\n|\r|\n)/g, "<br />"));

  return (
    <Box
      bgcolor="#F7F8FA"
      flexGrow={1}
      display="flex"
      // position="relative"
    >
      <ChatMainContent>
        <ChatHeader>
          <ChatHeaderStart>
            <ChatTitle>
              <Typography component="h4">{"Parag Rudani"}</Typography>
              <Typography component="span">{"via WhatsApp"}</Typography>
            </ChatTitle>
          </ChatHeaderStart>
          <ChatHeaderEnd>
            <LabeledIconButton
              label="Assign"
              icon={<UserAdd sx={{ height: 14, width: 14 }} />}
              iconStart
            />
            <IconButton
              sx={{ color: "#00000080", display: { lg: "none" } }}
              onClick={() => setShowInfoTab((showInfoTab) => !showInfoTab)}
            >
              <Info />
            </IconButton>
          </ChatHeaderEnd>
        </ChatHeader>
        <ChatArea>
          <MessageOut>
            <Message className="message" sx={MessageSx}>
              {formatMessage(
                "Heres a list of Honda Scooter that will always keep you on the go!\n\nPlease select a product of your choice.\n\n\nReply 1 for Activa 125 \n\nReply 2 for Activa 6G \n\nReply 3 for Dio \n\nReply 4 for Grazia \n\nReply 5 for Activa 125 \n\n ____\n\n Reply 0 to go back to main menu\n Reply # to go back"
              )}
              <Typography component="span">{"4:34 PM"}</Typography>
            </Message>
          </MessageOut>
          <MessageIn>
            <Message className="message" sx={MessageSx}>
              {formatMessage(
                "Lorem ipsum dolor sit amet consectetur, adipisicing elit. Consectetur, aperiam obcaecati, autem accusamus quaerat veritatis vero reprehenderit veniam nulla modi ratione quia consequuntur odit debitis quibusdam reiciendis ipsum rerum suscipit?"
              )}
              <Typography component="span">{"4:30 PM"}</Typography>
            </Message>
          </MessageIn>
          <MessageIn>
            <Message className="message" sx={MessageSx}>
              {formatMessage("Dwarka Sector, 25")}
              <Typography component="span">{"4:30 PM"}</Typography>
            </Message>
          </MessageIn>
          <MessageOut>
            <Message className="message" sx={MessageSx}>
              {formatMessage("Area")}
              <Typography component="span">{"4:30 PM"}</Typography>
            </Message>
          </MessageOut>
          <MessageIn>
            <Message className="message" sx={MessageSx}>
              {formatMessage("Delhi")}
              <Typography component="span">{"4:30 PM"}</Typography>
            </Message>
          </MessageIn>
          <MessageOut>
            <Message className="message" sx={MessageSx}>
              {formatMessage("City")}
              <Typography component="span">{"4:30 PM"}</Typography>
            </Message>
          </MessageOut>
          <MessageIn>
            <Message className="message" sx={MessageSx}>
              {formatMessage("Rohit")}
              <Typography component="span">{"4:30 PM"}</Typography>
            </Message>
          </MessageIn>
          <TimeBadge>
            <Chip size="small" label="Today" />
          </TimeBadge>
        </ChatArea>
        {false && (
          <ChatInfoFooter>
            <ChatFooterStart>
              <FooterHeading>
                <Clock />
                <Typography component="h4">24 Hour Window Elapsed</Typography>
              </FooterHeading>
              <FooterDescription>
                <Typography component="p">
                  You can initiate conversation by sending a template message or
                  wait for the user to reply.
                </Typography>
              </FooterDescription>
            </ChatFooterStart>
            <ChatFooterEnd>
              <LabeledIconButton
                label="Send Template"
                icon={<Send sx={{ height: 14, width: 14 }} />}
                sx={{ columnGap: 1.25 }}
              />
              <FooterDescription>
                <Typography component="p">
                  Charges applicable as per your plan.
                </Typography>
              </FooterDescription>
            </ChatFooterEnd>
          </ChatInfoFooter>
        )}
        <ChatBoxFooter>
          <ChatBoxAbove>
            <ChatBoxStart>
              {ChatBoxActions.map(({ icon }, index) => (
                <Button
                  variant="text"
                  key={index}
                  sx={ChatBoxStartActionSx}
                  startIcon={icon}
                />
              ))}
            </ChatBoxStart>
            <LabeledIconButton
              color="primary"
              disableFocusRipple
              disableRipple
              disableTouchRipple
              label="Send Template"
              icon={<Bot sx={{ height: 14, width: 14 }} />}
              iconStart
              variant="text"
            />
          </ChatBoxAbove>
          <ChatBoxBelow>
            {/* ChatInputBox */}
            <TextField
              placeholder="Write a Message..."
              multiline
              variant="standard"
              sx={{
                py: 0,
              }}
            />
            <IconButton
              sx={{ color: "#12B0EE", mb: 1 }}
              disableFocusRipple
              disableTouchRipple
              disableRipple
            >
              <Send sx={{ width: undefined, height: undefined }} />
            </IconButton>
          </ChatBoxBelow>
        </ChatBoxFooter>
      </ChatMainContent>
      <ChatInfo />
    </Box>
  );
};

export default ChatMain;
